* {
	box-sizing: border-box;
}

/* --color__dark-rgb: 32, 39, 47; */

:root {
	--color__light: #FDFDFE;
	--color__light-alpha: rgba(253, 253, 254, 0.1);
	--color__light-alpha-norm: rgba(253, 253, 254, 0.4);
	--color__dark: #20272F;
	--color__dark-alpha: rgba(32, 39, 47, 0.1);
	--color__base-red: rgb(160, 61, 41);
	--color__complementary: rgb(41, 140, 160);
	--ease-in-out-circle: cubic-bezier(0.785, 0.135, 0.150, 0.860);
	--font-scaling-ratio: 1;
}

::selection {
	text-shadow: none;
	color: var(--color__dark);
	background-color: var(--color__light);
}

html {
	-webkit-tap-highlight-color: transparent;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	touch-action: manipulation;
}

html,
body {
	min-height: 100%;
}

body {
	font-family: BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
	font-size: 100%;
	font-weight: 300;
	line-height: 1.5;
	min-width: 320px;
	background-color: var(--color__light);
	color: var(--color__dark);
	margin: 0;
}

.fonts-loaded body {
	font-family: 'Anonymous Pro', monospace;
}

.fonts-loaded h1,
.fonts-loaded h2,
.fonts-loaded h3,
.fonts-loaded h4 {
	font-family: 'Montserrat', sans-serif;
}

h1 {
	margin: 0;
	font-size: calc(4vw * var(--font-scaling-ratio));
}

h1, h2, h3, h4 {
	font-weight: 300;
}

button {
	padding: 0;
	border: 0;
	background: none;
	cursor: pointer;
}

button:focus {
	outline: none;
}

svg {
	display: inline-block;
	transition: 0.25s transform var(--ease-in-out-circle);
}

path,
rect {
	transition: fill, stroke-dash-array, stroke-dash-offset 0.2s var(--ease-in-out-circle);
}

figure {
	margin: 0;
}

a {
	color: inherit;
	/*border-bottom: 1px solid;*/
	text-decoration: none;
	position: relative;
	transition: border-bottom-color 0.25s ease-out;
}

a::after {
	content: '';
	position: absolute;
	width: 100%;
	top: 0;
	left: 0;
	bottom: 0;
	background-color: rgba(32, 39, 47, 0.1);
	transform: scale(1, 0.03);
	transition: opacity, transform 0.3s var(--ease-in-out-circle);
	opacity: 1;
	transform-origin: bottom;
}

.no-touchevents a:hover::after {
	opacity: 1;
	transform: scale(1) rotate(-2.3deg);
}

.no-touchevents a:hover {
	/*border-bottom-color: rgba(0, 0, 0, 0);*/
}

.v-enter-active,
.v-leave-active {
	transition: opacity 1s var(--ease-in-out-circle);
}

.v-enter,
.v-leave-active {
	opacity: 0;
}

.app-wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.page-wrap {
	padding: 2vmax 5vmax;
}

.u-text--center {
	text-align: center;
}

.u-text--right {
	text-align: right;
}

